import React, { Component } from 'react'
import { CLOUDPATH, CLOUDPATH_TAGGSHOP } from '../../../actions/api';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import ReactHtmlParser from 'react-html-parser';
import { togglePlanSection } from '../../../actions/show_modal_Action';
import { UpdateTimeModal } from './elements/UpdateTimeModal';
import { HeaderSupport } from './elements/HeaderSupport';
import { HeaderCollabrator } from './elements/HeaderCollabrator'; 
class headerMenu extends Component {
    state = { openMenu: false, showPopUp: false }

    openSidebarToggle = () => {
        const { openMenu } = this.state;
        if (!openMenu) {
            document.body.classList.add('aside-minimize');
            this.setState({ openMenu: true })
        } else {
            document.body.classList.remove('aside-minimize');
            this.setState({ openMenu: false })
        }
    }

    topMobileOn = () => {
        const body = document.getElementsByTagName("body")[0].classList.toggle("topbar-mobile-on");
        const menu = document.getElementById("kt_header_menu_wrapper");
        const overlay = document.getElementById("product_switcher_overlay_mobile");
        const aside = document.getElementById("kt_aside_mobile_toggle");

        if (body.classList.contains('topbar-mobile-on')) {
            body.classList.remove('topbar-mobile-on');
        } else {
            body.classList.add('topbar-mobile-on');
        }

        if (menu.classList.contains('header-menu-wrapper-on')) {
            body.classList.remove('header-menu-wrapper-on');
        } else {
            body.classList.add('header-menu-wrapper-on');
        }

        if (overlay.classList.contains('header-menu-wrapper-overlay')) {
            overlay.classList.remove('header-menu-wrapper-overlay');
        } else {
            overlay.classList.add('header-menu-wrapper-overlay');
        }

        if (aside.classList.contains('kt_aside_mobile_toggle')) {
            aside.classList.remove('kt_aside_mobile_toggle');
        } else {
            aside.classList.add('kt_aside_mobile_toggle');
        }

    }

    headerMenuWrapperOn = () => {
        const list = document.getElementById("kt_header_menu_wrapper").classList.toggle("header-menu-wrapper-on");
        document.getElementById("product_switcher_overlay_mobile").classList.toggle('header-menu-wrapper-overlay');
    }


    asideOn = () => {
        const list = document.getElementById("kt_aside_mobile_toggle").classList;
        document.getElementById("kt_aside").classList.toggle('aside-on');
    }

    getNotificationAction = (action, getNotificationUrl, message) => {
        if (action === 'init') {
            return <a href={`${getNotificationUrl}`} className={`resetAction ${action != 'init' ? action : ''}`}>
                {ReactHtmlParser(message)}
            </a>
        } else if (action === 'popup') {
            return <a className={`resetAction ${action != 'init' ? action : ''}`} onClick={() => this.togglePlanSection(true)}>
                {ReactHtmlParser(message)}
            </a>
        } else if (action === 'ugc') {
            return <a href={getNotificationUrl} className={`resetAction ${action != 'init' ? action : ''}`} target='_blank'>
                {ReactHtmlParser(message)}
            </a>
        }
        return <a href={`${getNotificationUrl}`} className={`resetAction ${action != 'init' ? action : ''}`}>
            {ReactHtmlParser(message)}
        </a>
    }

    getUnreadNotification = (list) => {
        return list.reduce((acc, curr) => {
            if (curr.status === 0) {
                acc++;
            }
            return acc;
        }, 0)
    }
    modalHandleShow = () => this.setState({ showPopUp: true })
    modalhandlehide = () => this.setState({ showPopUp: false })

    render() {
        const { authorized, latestNotifionList } = this.props
        const { showPopUp } = this.state
        const user = authorized?.userData?.user_data.user
        const collabratorList = user && Object.keys(user).length > 0 ? user.collabratorList : [];
        const profileImg = user?.imgSource ? `${CLOUDPATH}${user?.picture}` : user?.picture
        const wallLimitExceed = JSON.parse(localStorage.getItem('wallLimitExceed'))
        const user_name = user?.name
       
        return (<>
            <div id="kt_header" className="header header-fixed price_header">
                <div className=" container-fluid  d-flex align-items-stretch justify-content-between">
                    <div className="header-menu-wrapper header-menu-wrapper-left" id="kt_header_menu_wrapper">
                        <div id="kt_header_menu" className="header-menu header-menu-mobile header-menu-layout-default align-items-center">
                            <div className="brand flex-column-auto ">
                                <div className="brand-logo">
                                    <img src="https://cloud.taggbox.com/taggshop/assets/images/home-page/logo_new.svg" alt='logo-image'/>
                               </div>
                            </div>
                        </div>
                    </div>

                    <div className="topbar">
                       <HeaderSupport />  

                        {   /* //?-------------------------profile--------------------- */}
                        <div className="topbar-item">
                            <div className="btn btn-icon-mobile w-auto d-flex align-items-center btn-lg p-0" id="kt_quick_user_toggle" data-bs-toggle="offcanvas" data-bs-target="#kt_quick_user" aria-controls="offcanvasRight">
                                <span className="symbol symbol-lg-45 symbol-25 symbol-circle symbol-light-warning symbol-relative userProfilePicture">
                                    <span className="premium-user-sign userLoginStar">
                                        <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="20" height="20" viewBox="0 0 20 20">
                                            <defs>
                                                <linearGradient id="linear-gradient" x1="0.5" y1="1.077" x2="0.5" gradientUnits="objectBoundingBox">
                                                    <stop offset="0" stopColor="#fd8800"></stop>
                                                    <stop offset="1" stopColor="#fec400"></stop>
                                                </linearGradient>
                                                <linearGradient id="linear-gradient-2" x1="0.5" y1="1" x2="0.5" gradientUnits="objectBoundingBox">
                                                    <stop offset="0" stopColor="#ffe59a"></stop>
                                                    <stop offset="1" stopColor="#ffffd5"></stop>
                                                </linearGradient>
                                            </defs>
                                            <g id="Group_4832" data-name="Group 4832" transform="translate(6313.593 292.593)">
                                                <g id="Group_4635" data-name="Group 4635" transform="translate(-6313.593 -292.593)">
                                                    <g id="Group_4634" data-name="Group 4634" transform="translate(0 0)">
                                                        <circle id="Ellipse_183" data-name="Ellipse 183" cx="10" cy="10" r="10" fill="url(#linear-gradient)"></circle>
                                                    </g>
                                                </g>
                                                <g id="star" transform="translate(-6310.039 -289.039)">
                                                    <path id="Path_4779" data-name="Path 4779" d="M7.018.15,8.581,3.631l3.8.391a.634.634,0,0,1,.355,1.1L9.9,7.716l.781,3.73a.62.62,0,0,1-.924.675L6.414,10.2l-3.3,1.918a.62.62,0,0,1-.924-.675l.781-3.73L.127,5.123a.634.634,0,0,1,.355-1.1l3.8-.391L5.846.15A.63.63,0,0,1,7.018.15Z" transform="translate(0.089 0.25)" fillRule="evenodd" fill="url(#linear-gradient-2)"></path>
                                                </g>
                                            </g>
                                        </svg>
                                    </span>
                                    {profileImg ? <div className="symbol-label" style={{ backgroundImage: `url('${profileImg}')` }}></div>
                                    :
                                    <span className="symbol-label font-size-h5 font-weight-bold text-uppercase rounded-2">{user_name ? user_name[0]?.toUpperCase() : ""}</span>}
                                </span>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        authorized: state.authorized,
        latestNotifionList: state.notification.latestNotifionList
    }
}

const mapDispatchToProps = dispatch => {
    return {
        togglePlanSection: (state) => dispatch(togglePlanSection(state))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(headerMenu)